export default ()=> {
      return (
        <div className="question" data-index="147" dangerouslySetInnerHTML = {{ __html: `
<h6>147. 输出什么？</h6>
<pre class="language-javascript"><code class="language-javascript"><span class="token keyword keyword-class">class</span> <span class="token class-name">Calc</span> <span class="token punctuation">{</span>
	<span class="token function">constructor</span><span class="token punctuation">(</span><span class="token punctuation">)</span> <span class="token punctuation">{</span>
		<span class="token keyword keyword-this">this</span><span class="token punctuation">.</span>count <span class="token operator">=</span> <span class="token number">0</span> 
	<span class="token punctuation">}</span>

	<span class="token function">increase</span><span class="token punctuation">(</span><span class="token punctuation">)</span> <span class="token punctuation">{</span>
		<span class="token keyword keyword-this">this</span><span class="token punctuation">.</span>count <span class="token operator">++</span>
	<span class="token punctuation">}</span>
<span class="token punctuation">}</span>

<span class="token keyword keyword-const">const</span> calc <span class="token operator">=</span> <span class="token keyword keyword-new">new</span> <span class="token class-name">Calc</span><span class="token punctuation">(</span><span class="token punctuation">)</span>
<span class="token keyword keyword-new">new</span> <span class="token class-name">Calc</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">.</span><span class="token function">increase</span><span class="token punctuation">(</span><span class="token punctuation">)</span>

console<span class="token punctuation">.</span><span class="token function">log</span><span class="token punctuation">(</span>calc<span class="token punctuation">.</span>count<span class="token punctuation">)</span>
</code></pre>
<ul>
<li>A: <code>0</code></li>
<li>B: <code>1</code></li>
<li>C: <code>undefined</code></li>
<li>D: <code>ReferenceError</code></li>
</ul>
<details><summary><b>答案</b></summary>
<p>
<h4>答案: A</h4>
<p>我们设置 <code>calc</code> 变量为 <code>Calc</code> 类的一个新实例。 然后，我们初始化一个 <code>Calc</code> 的新实例，而且调用了这个实例的 <code>increase</code> 方法。因为count属性是在 <code>Calc</code> class的constructor内部的，所以count属性不会在 <code>Calc</code> 的原型链上共享出去。这就意味着calc实例的count值不会被更新，count仍然是 <code>0</code>。</p>
</p>
</details>
` }}></div>
      );
    }